.regField{
    width: 100%;
    background-color: #f7f7f7;
    color:#000;
    border: 1px solid #ddd;
    padding: 15px;
    box-sizing: border-box;
    font-size: 15px;

}

.submitBtn{
    width: 100%;
    background: #425f9c;
    border: 0;
    color: #fff;
    padding: 15px;
    margin: 10px 0 0 0;
    font-size: 15px;
    cursor: pointer;

}
/* Customized to make underLine and linkBtn the same font-size of 15 */
.underLine{
    text-align: center;
    margin: 7px 0 0 0 ;
    display: block;
    font-size: 15px;
    color: #3c4043;
}

.linkBtn{
    border: 0;
    background-color: unset;
    text-decoration: underline;
    color: #3c4043;
    padding: 0;
    margin: 0;
    font-size: 15px;

}
.Error{
    padding: 10px;
    background-color: #feeaea;
    border: 1px solid #fadadb;
    margin-bottom: 10px;
    border-radius: 3px;
    font-size: 15px;
}